import React, { useEffect, useState } from "react";
import RoleRoutes from "../../componets/shared/ProtectedRoute/RoleRoutes";
import MiniStudioShell from "../../componets/cms/MiniStudioShell";
import CandidatesFrame from "../../componets/cms/mini-studio/CandidatesFrame";
import { ChevronLeft, ChevronRight } from "lucide-react";
import { getPaginatedRows } from "../../forms/google-sheets-api";

export default function CandidatesPage() {
  const [candidatesData, setCandidatesData] = useState<any>(null);
  const [pagination, setPagination] = useState({ page: 1, totalPages: 1 });
  const [loadingCandidates, setLoadingCandidates] = useState(false);

  async function loadCandidates(p: number) {
    try {
      setLoadingCandidates(true);
      const result = await getPaginatedRows({ page: p, limit: 10 });
      setCandidatesData(result);
      setPagination({ page: result.page, totalPages: result.totalPages });
    } catch (err) {
      console.error("üí• Failed to load candidates:", err);
    } finally {
      setLoadingCandidates(false);
    }
  }

  useEffect(() => {
    loadCandidates(1);
  }, []);

  const nextPage = () => {
    if (pagination.page < pagination.totalPages) {
      loadCandidates(pagination.page + 1);
    }
  };

  const prevPage = () => {
    if (pagination.page > 1) {
      loadCandidates(pagination.page - 1);
    }
  };

  return (
    <RoleRoutes role="admin">
      <MiniStudioShell>
        {() => (
          <>
            {loadingCandidates ? (
              <div style={{ padding: 40, textAlign: "center" }}>
                ‚è≥ Loading candidates‚Ä¶
              </div>
            ) : (
              <>
                <CandidatesFrame candidatesData={candidatesData} />
                {candidatesData && candidatesData.totalPages > 1 && (
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      gap: "10px",
                      padding: "12px",
                      borderTop: "1px solid var(--border)",
                    }}
                  >
                    <button
                      onClick={prevPage}
                      disabled={pagination.page <= 1}
                      style={{
                        opacity: pagination.page <= 1 ? 0.5 : 1,
                        cursor: pagination.page <= 1 ? "not-allowed" : "pointer",
                      }}
                    >
                      <ChevronLeft size={14} /> Prev
                    </button>

                    <span style={{ fontSize: "12px" }}>
                      Page {pagination.page} / {pagination.totalPages}
                    </span>

                    <button
                      onClick={nextPage}
                      disabled={pagination.page >= pagination.totalPages}
                      style={{
                        opacity: pagination.page >= pagination.totalPages ? 0.5 : 1,
                        cursor:
                          pagination.page >= pagination.totalPages
                            ? "not-allowed"
                            : "pointer",
                      }}
                    >
                      Next <ChevronRight size={14} />
                    </button>
                  </div>
                )}
              </>
            )}
          </>
        )}
      </MiniStudioShell>
    </RoleRoutes>
  );
}
